}

body {
  font-family: "Roboto Mono", monospace;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.0625rem;
}

h1 {
  font-size: 3.125rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 1.875rem;
}

:root {
  --bg-color: hsl(238, 56%, 9%);
  --pink: hsl(323, 51%, 50%);
  --primary: hsl(212, 100%, 50%);
  --dark-primary: rgba(56, 139, 253, 0.2);
  --white: #ffffff;
  --info: #314485;
  --gray: #848d97;
  --success: #6cc644;
  --warning: #c9510c;
  --danger: #bd2c00;
  --dark: #333;
}

body {
  background-color: var(--bg-color);
  color: var(--white);
}

.container {
  max-width: 53.125rem;
  margin: 0 auto;
  padding: 0 0.9375rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
@media (max-width: 775px) {
  .container {
    display: none;
  }
}

.device {
  margin: 30px auto;
  padding: 0 0.9375rem;
  text-align: center;
}
@media (min-width: 775px) {
  .device {
    display: none;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 3.125rem;
  margin-top: 7.8125rem;
}
.header h1 {
  font-size: 3.4375rem;
  font-weight: 800;
  line-height: 4rem;
  background: linear-gradient(to right, var(--pink) 0%, var(--primary) 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.search {
  width: 100%;
  margin: 0 auto;
  margin-bottom: 2.5rem;
}
.search .search-container {
  background-color: var(--bg-color);
  border: 0.1875rem solid var(--pink);
  border-radius: 0.9375rem;
  height: 3.75rem;
  display: flex;
  overflow: hidden;
}
.search .search-container .input-container {
  flex-grow: 1;
  display: flex;
  align-items: center;
}
.search .search-container .input-container input {
  flex-grow: 1;
  background-color: transparent;
  color: white;
  font-size: 1.375rem;
  margin-left: 1.25rem;
  padding-right: 0.625rem;
}
.search .search-container .input-container svg {
  width: 1.5625rem;
  height: 1.5625rem;
  color: var(--gray);
  margin-left: 1.5625rem;
}
.search .search-container .search-btn {
  background: linear-gradient(to left, var(--pink), var(--info));
  width: 9.375rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.375rem;
  cursor: pointer;
}

#profile-card {
  width: 100%;
  margin-bottom: 7.5rem;
}
#profile-card .user-card {
  padding: 3.125rem;
  background: var(--bg-color);
  color: var(--white);
  border: 0.1875rem solid var(--primary);
  border-radius: 0.9375rem;
  display: flex;
  margin-bottom: 1.25rem;
}
#profile-card .user-card .user-avatar {
  margin-right: 1.875rem;
}
#profile-card .user-card .user-avatar img {
  min-width: 7.5rem;
  height: 7.5rem;
  border-radius: 50%;
  object-fit: cover;
}
#profile-card .user-card .user-detail {
  display: flex;
  flex-direction: column;
}
#profile-card .user-card .user-detail .user-info {
  margin-bottom: 3.125rem;
}
#profile-card .user-card .user-detail .user-info .name {
  font-size: 1.6875rem;
  font-weight: 700;
  margin-bottom: 0.9375rem;
}
#profile-card .user-card .user-detail .user-info .user-joined-at {
  font-size: 1.0625rem;
  font-weight: 500;
  margin-bottom: 0.4375rem;
}
#profile-card .user-card .user-detail .user-info .username {
  font-size: 1rem;
  font-weight: 500;
  color: var(--primary);
}
#profile-card .user-card .user-detail .user-info .bio {
  margin-top: 0.375rem;
  font-size: 1.125rem;
  font-weight: 500;
}
#profile-card .user-card .user-detail .user-status {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
#profile-card .user-card .user-detail .user-status .stat {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
#profile-card .user-card .user-detail .user-status .stat .title {
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 0.3125rem;
}
#profile-card .user-card .user-detail .user-status .stat .value {
  font-size: 1.1875rem;
  font-weight: 700;
}
#profile-card .user-card .user-detail .user-contact {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.5rem;
}
#profile-card .user-card .user-detail .user-contact .contact {
  display: flex;
  width: 50%;
  margin-bottom: 0.9375rem;
}
#profile-card .user-card .user-detail .user-contact .contact .icon {
  margin-right: 1rem;
}
#profile-card .user-card .user-detail .user-contact .contact .icon svg {
  width: 1.6875rem;
  height: 1.6875rem;
  color: var(--gray);
}
#profile-card .user-card .user-detail .user-contact .contact span {
  font-size: 1.125rem;
  font-weight: 500;
}
#profile-card .user-card .user-detail #user-repos {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.5rem;
}
#profile-card .user-card .user-detail #user-repos .repo {
  padding: 0.3125rem 0.5rem;
  background-color: var(--dark-primary);
  margin: 0 0.5rem 0.625rem 0;
  color: var(--primary);
  border-radius: 0.3125rem;
  font-size: 0.9375rem;
  font-weight: 500;
}

.alert {
  padding: 0.75rem;
  font-size: 1.125rem;
  font-weight: 600;
  border-radius: 0.3125rem;
}
@media (max-width: 487px) {
  .alert {
    font-size: 0.875rem;
  }
}
.alert-warning {
  background-color: rgba(255, 171, 0, 0.2);
  color: #c28200;
  border: 1px solid #eb9d00;
}
.alert-primary {
  background-color: rgba(0, 144, 231, 0.2);
  color: #006db0;
  border: 1px solid #0084d5;
}
.alert-danger {
  background-color: rgba(252, 66, 74, 0.2);
  color: #c03238;
  border: 1px solid #e83d44;
}
.alert-info {
  background-color: rgba(143, 95, 232, 0.2);
  color: #6d48b0;
  border: 1px solid #8457d5;
}
.alert-success {
  background-color: rgba(0, 210, 91, 0.2);
  color: #00a045;
  border: 1px solid #00c154;
}
.alert-light {
  background-color: hsla(0deg, 0%, 100%, 0.2);
  color: #c2c2c2;
  border: 1px solid #ebebeb;
}

.footer {
  margin: 20px;
}
.footer p {
  font-size: 18px;
  font-weight: 600;
}
.footer p a {
  color: var(--primary);
}